.prod-page-section-one-container {
    border-bottom: 1px solid lightgray;
    display: grid;
    grid-template-columns: auto auto;
    min-width: 300px;
}

.prod-page-figure-section {
    display: grid;
    margin-top: auto;
    margin-bottom: auto;
}

.product_page_items_info {
    display: grid;
}

.product_name_label {
    font-family: 'Inter';
    font-size: larger;
    font-weight: 500;
    color: #2957CB;
    display: flex;
}

.product_desc_text {
    font-size: smaller;
}

@media screen and (max-width: 900px) {
    .prod-page-section-one-container {
        display: block;
        margin-right: auto;
        margin-left: auto;
        text-align: center;

    }

    .product_name_label {
        display: block;
    }

    .prod_page_sec_img-wrap {
        margin-inline-start: 0;
    }


}
