/* for less than 1100 px, make the backgroun image centered */
.hero-container {
    background: url('./images/hero-background-image.png') center center/cover no-repeat;
    background-size: 872px 586px;
    background-position: right;
    background-color: #0F172D;
    color: #fff;
    font-family: 'Inter';
    min-height: 500px;
}

.protecting-header {
    border-bottom: 4px solid #2957CB;
    display: inline-block;
    padding: 8px;
    font-size: xxx-large;
    margin-block-start: 0px;
    margin-block-end: 0px;
    font-weight: bold
}

.hero-grid {
    margin-left: 50px;
    margin-block-start: 0px;
}

.hero-grid li {
    display: inline-block;
    align-items: start;
}

.protecting-paragraph {
    width: 650px;
    font-size: 18px;
    margin-block-start: 10px;
    margin-block-end: 20px;
}

.hero-sections {
    border: 0px;
    overflow: hidden;
    float: left;
    padding: 10px;
    margin-right: 10%;
    margin-left: 50px;
    width: -webkit-fill-available;
    margin-top: 30px;
}

.section-1 {
    float: left;
    margin-top: 4em;
}

.section-2 {
    float: right;

}

@media screen and (max-width: 1230px) {
    .hero-container {
        background: url('./images/hero-background-image.png') center center/cover no-repeat;
        background-color: #0F172D;
        color: #fff;
        font-family: 'Inter';
        min-height: 500px;
    }

    .small-container {
        display: block;
    }

    .protecting-header {
        border-bottom: none;
        margin: 1em;
        text-align: center;
        display: block;
        padding: 8px;
        font-size: 48px;
        margin-block-start: 0px;
        margin-block-end: 25px;
        font-weight: bold
    }

    .section-2 {
        float: none;
        text-align: center;
    }

    .protecting-paragraph {
        font-size: medium;
        align-items: center;
        text-align: center;
        margin-right: auto;
        margin-left: auto;
        max-width: 60%;
        min-width: 300px;
    }

    .contact-button-div {
        align-items: center;
        display: table;
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 2em;
    }
}